.grid_layout{
    background: #1a2a6c;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, #fdbb2d, #b21f1f, #1a2a6c);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #fdbb2d, #b21f1f, #1a2a6c); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    width: 100%;
    height: 280vh;
    text-align: center;
 
}

.aavtarImg{
    margin-top: 15px;
    margin-bottom: -115px;
    height: 250px;
    object-fit: contain;
    border: 0.5px solid #fff;
    border-color: white;
    border-radius: 50%; 
    transition: transform .2s;
    box-shadow: 2px 2px 10px;
    animation: rollIn; /* referring directly to the animation's @keyframe declaration */
    animation-duration: 2s;
 
}

/* .aavtarImg:hover{
    margin-top: 150px;
    transform: scale(1.5);
} */

.text1{
    padding-top: 40px;
    padding-bottom: 10px;
    font-weight: bold;
    text-align: center;
    opacity: 1.0;
    color: white;
    margin: auto;
    margin-bottom: 10px;
    margin-top: 10px;
    animation: bounceInUp; /* referring directly to the animation's @keyframe declaration */
    animation-duration: 2s;
}

.hr1{
    margin-top: 10px;
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;
}

.div1{
    margin: auto;
    width: 75%;
    color: white;
    border-radius: 10px;
    background-color: black;
    animation: rollIn; /* referring directly to the animation's @keyframe declaration */
    animation-duration: 1s;
}


.txt2{
    opacity: 1.0;
    color: white;
    padding-top: 10px;
    text-align: center;
    margin-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
}

.Social_links{
    display: flex;
    justify-content: space-between;
    width: 20%;
    margin: auto;
}

.Social_links i{
    margin-bottom: 15px;
    color: white;
    font-size: 6em;
    animation: rollIn; /* referring directly to the animation's @keyframe declaration */
    animation-duration: 4s;
}


@media only screen and (max-width: 600px) {
    .div1{
        padding-top: 55px;
        margin-top: 25px;
        width: 90%;
        color: white;
        border-radius: 10px;
        background: linear-gradient(to left, #fdbb2d, #b21f1f, #1a2a6c); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
       box-shadow: 0px 0px 20px black;
    }

    .Social_links{
        display: flex;
        justify-content: space-between;
        width: 50%;
        margin: auto;
    }
    
    .Social_links i{
        margin-bottom: 15px;
        color: white;
        font-size: 6em;
    }
}