.contact_div{
    margin-left: auto;
    margin-right: auto;
    height: 590px;
    justify-content: center;
    margin-top: 4%;
    
    padding-bottom: 35px;
    font-family: 'Oxygen', sans-serif;
}

.cardHere{

    box-shadow: 0px 0px 20px black;
}
.contact_grid{

    text-align: center;
    justify-content: center;
    width: 80%;
    height: 550px;
    background: white;
    color: black;
    margin-bottom: 20px;
}

.contact_grid h2{
    font-family: 'Anton', sans-serif;
}

.contact_grid p{
    font-family:     'Oxygen', sans-serif;

    font-weight: bold;
}

.someText{
    margin: auto;
    margin-top: 150px;
    background: linear-gradient(to left, #fdbb2d, #b21f1f, #1a2a6c); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
}

.contact_grid hr{
    border-top: 5px dotted black;
    width: 50%;
    margin: auto;
}

.contact_list{
    margin: auto;
    display: inline-block;
    background: linear-gradient(to left, #fdbb2d, #b21f1f, #1a2a6c); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
}

.item1{
    font-weight: bold;
     color: black;
     font-size: 20px;
     text-align: center;
     padding-top: 15px;
}