progress {
    width: 85%;
    height: 16px;
    display: block;
    /* disable defaults */
    -webkit-appearance: none;
    border: none;
    }
    /*Styling the background of the progress bar */
    progress::-webkit-progress-bar {
    background: rgb(255, 255, 255);
    border-radius: 45px;
    padding: 2px;
    box-shadow: 0 1px 0px 0 rgba(255, 255, 255, 0.2);
    }
    /* Setting the appearance of the meter */
    progress::-webkit-progress-value {
    border-radius: 25px;
    box-shadow: inset 0 1px 1px 0 rgba(255, 255, 255, 0.4); 
    background-size: 250px 25px;
    background-image: linear-gradient(135deg, rgba(255, 255, 255, .15) 25%, transparent 25%,
    transparent 75%, rgba(255, 255, 255, .15) 75%, rgba(255, 255, 255, .15) 75%,
    transparent 75%, transparent);
    }

    .cardI{
        background: #1a2a6c;  /* fallback for old browsers */
        background: -webkit-linear-gradient(to left, #fdbb2d, #b21f1f, #1a2a6c);  /* Chrome 10-25, Safari 5.1-6 */
        background: linear-gradient(to left, #fdbb2d, #b21f1f, #1a2a6c); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
       
    }